import React from "react";
// import PropTypes from "prop-types";
import { CardKamar, Carousel, LowMemoryError } from "../../components";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import { makeStyles } from "@mui/styles";
// import { getOnboard } from "../../redux/actions";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import doctorImg from "../../assets/doctors.gif";

const useStyles = makeStyles({
  rounded: {
    borderRadius: "10px !important",
    width: "100%",
  },
  root: {
    height: "100vh",
  },
});

const OnBoardingPage = (props) => {
  const { error } = props;

  const navigate = useNavigate();

  if (error) {
    navigate("/exception-handling");
  }
  const classes = useStyles();
  const toRoom = () => {
    navigate("/list-room");
  };
  const toRegister = () => {
    navigate("/register");
  };
  React.useEffect(() => {
    // actGetOnboard();
  }, []);
  let carousel = [];
  [0].map((item) => {
    carousel.push({
      image: doctorImg,
      link: "https://storyset.com/people",
      text: "How can we help you?",
      title: "Hi, Welcome aboard!",
    });
  });
  const errorAtt = localStorage.getItem("registerAttempt")
    ? parseInt(localStorage.getItem("registerAttempt")) > 0 &&
      parseInt(localStorage.getItem("registerAttempt")) % 3 === 0
      ? true
      : false
    : false;
  const [lowError, setLowError] = React.useState(errorAtt);
  return (
    <Container>
      <LowMemoryError open={lowError} setOpen={setLowError} />
      <Grid
        container
        spacing={4}
        className={classes.root}
        justifyContent="center"
        alignItems="center"
      >
        <Grid item xs={12}>
          <div className={classes.rounded}>
            <Carousel type="big" carousel={carousel} />
          </div>
        </Grid>
        <Grid container item xs={12} spacing={2}>
          <Grid item xs={12}>
            <Button
              variant="contained"
              fullWidth
              onClick={toRegister}
              className={classes.buttonFoot}
            >
              DAFTAR PASIEN BARU / REGISTER
            </Button>
          </Grid>
          <Grid item xs={12}>
            <Button
              variant="outlined"
              fullWidth
              onClick={toRoom}
              className={classes.buttonFoot}
            >
              DATA KAMAR / AVAILABLE ROOMS
            </Button>
          </Grid>
        </Grid>
      </Grid>
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    error: state.app.error,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    // actGetOnboard: (body, action) => () => {},
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(OnBoardingPage);
